* {
    margin: 0;
    padding: 0;
    color: #232233;
    font-size: 62.5%; /* 1rem = 10px */
    border: 0;
    outline: 0;
    box-sizing: border-box;
}

button {
    display: inline-block;
    height: 40px;
    padding: 5px 10px;
    line-height: 1.5;
    background: #cae9f6;
    border-radius: 10px;
}

.nav {
    display: flex;
    justify-content: center;
    padding: 20px;
    color: #FFF;
    background-color: #232323;
}

.list-wrap {
    display: flex;
    flex-direction: column;
    padding: 20px;

    li {
        display: flex;
        gap: 10px;
        align-items: center;
        justify-content: space-between;
        
        + li {
            border-top: 1px solid #BFBFBF;
        }

        .post-wrap {
            display: flex;
            flex-direction: column;
            gap: 10px;
            padding: 20px;
            
            h4 {
                font-size: 1.8rem;
                font-weight: 600;
            }
            p {
                color: #999;
                font-size: 1.4rem;
                font-weight: 300;
            }
        }

        button {
            width: 80px;
            font-weight: 600;
            background: #fbbdbd;
        }
    }
}

.btn-wrap {
    display: flex;
    gap: 10px;
    justify-content: center;
    background: #f7f7f7;

    .btn {
        width: 80px;
        margin: 20px 0;
    }
}

.form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    padding: 20px;

    .input-wrap {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 10px;

        input {
            width: 200px;
            height: 40px;
            padding: 5px 10px;
            font-size: 1.4rem;
            border: 1px solid #ddd;
            border-radius: 5px;
        }
    }
}

.modal {
    margin: 20px;
    padding: 20px;
    border: 1px solid #bfbfbf;
    border-radius: 10px;

    .modal-head {
        display: flex;
        justify-content: space-between;
        padding: 0 5px 1cap;
        border-bottom: 1px solid #BFBFBF;

        h4 {
            font-size: 1.8rem;
            font-weight: 600;
        }
        span {
            color: #999;
            font-size: 1.4rem;
            font-weight: 300;
        }
    }

    .modal-body {
        min-height: 200px;
        padding-top: 20px;
    }
}